'use server';

import { ai } from '@/ai/genkit';
import { MessageData, Part, streamable } from 'genkit';
import { z } from 'zod';

const ChatInputSchema = z.object({
  history: z.array(z.any()),
});

export async function chat(input: z.infer<typeof ChatInputSchema>): Promise<streamable.Stream<MessageData>> {
  const { history } = input;

  const systemPrompt = `You are a friendly and helpful chatbot assistant for an application called "My Abacus Pro".
      Your goal is to assist users with any questions they have about the app, abacus techniques, mental math, or their account.
      The app helps users practice mental arithmetic using abacus methods.
      Key features include:
      - Practice tests for addition, subtraction, multiplication, and division.
      - Special tests for abacus formulas: "Small Sister", "Big Brother", and "Combination".
      - "Beads Value" tests to practice recognizing numbers on an abacus.
      - A "Bubble Game" for a fun way to practice.
      - A "Progress Report" to track performance.
      - A "Tool Preview" to visualize numbers on an abacus.
      - Pro membership for unlimited access.
      Be concise and encouraging in your responses. If you don't know an answer, say so politely.`;

  const messages: Part[] = history.flatMap((msg: MessageData) => msg.content || []);

  const { stream } = await ai.generate({
    prompt: messages,
    system: systemPrompt,
    model: 'googleai/gemini-2.0-flash',
    stream: true,
  });

  return stream;
}
